@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
html,
body,
#root {
  margin: 0;
  font-family: "Poppins" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-image: url(/static/media/bg5.4694d0d6.png);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  color: #fff !important;
  overflow-y: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.link-container{width:100%;margin-bottom:16px}.link-container .link-button{text-align:center;width:100%;color:#fff;display:block;border:2px solid #fff;padding-top:13px;padding-bottom:13px;border-radius:20px}.link-container .link-button:hover{background-color:#fff;color:#631f48;cursor:pointer;text-decoration:none}

.home-container{margin-top:-40px;padding-top:20px}.home-container .avatar-wrapper{width:138px;height:138px;border-radius:138px;border:1px solid #efefef}.home-container .username{font-size:16px}.home-container .fullname{font-size:20px;font-weight:bold}.home-container .link-wrapper{width:100%;margin-top:24px}

