.link-container {
  width: 100%;
  margin-bottom: 16px;
  .link-button {
    text-align: center;
    width: 100%;
    color: #fff;
    display: block;
    border: 2px solid #fff;
    padding-top: 13px;
    padding-bottom: 13px;
    border-radius: 20px;

    &:hover {
      background-color: #fff;
      color: #631f48;
      cursor: pointer;
      text-decoration: none;
    }
  }
}
